
.button {
    margin-left: 20px;
    font-size: 20px;
    padding: 3px 20px;
    background-color: #000;
    color: #fff;
    letter-spacing: 2px;
    transition: .5s;
}

.button:hover {
    cursor: pointer;
    background-color: #fff;
    color: #000;
    transition: .5s;
}
.username__match {
    border: 1px solid #000;
    margin-top: 5px;
    padding: 3px;
    font-size: 18px;
    background-color: #fff;
}

.username__match {
    cursor: pointer;
}
.modal {
    display: flex;
    flex-direction: column;
    width: 50%;
    height: 60vh;
    position: fixed;
    left: 50%;
    top: 10%;
    transform: translate(-50%,0);
    z-index: 1000;
    transition: .3s;
}

.modal__heading {
    color: #fff;
    padding: 3% 0;
    font-weight: bold;
    font-size: 2.5rem;
    letter-spacing: 2px;
    text-align: center;
}

.modal__heading--alert {
    background-color: red;
}
.modal__heading--success {
    background-color: green;
}

.modal__message {
    flex-grow: 1;
    width: 100%;
    font-size: 24px;
    margin: 0 auto;
    color: #000;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
}

.modal__btn-div {
   flex-basis: 20%;
   display: flex;
   justify-content: center;
   align-items: center;
   background-color: rgb(238, 237, 237);
}

.modal__button {
    font-size: 22px;
    letter-spacing: 3px;
    padding: 5px 20px;
    border-radius: 5px;
}

.username__form-wrapper {
    display: flex;
    flex-direction: column;
}

.username__wrpper {
    display: flex;
    flex-direction: column;
}

.username__form {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin-top: 10vh;
}


.username__label {
    flex-basis: 100%;
    font-size: 25px;
    text-align: center;
    margin-bottom: 10px;
    font-weight: bold;
    text-transform: capitalize;
}


.username__input {
    font-size: 20px;
    padding: 3px;
    width: 50%;
}

.username__button {
    margin-left: 20px;
    font-size: 20px;
    padding: 3px 20px;
    background-color: #000;
    color: #fff;
    border-radius: 5px;
    transition: .5s;
}

.username__button:hover {
    cursor: pointer;
    background-color: #fff;
    color: #000;
    transition: .5s;
}

.username__button:disabled {
    color: rgb(207, 193, 193);
    background-color: #fff;
}
.username__button:disabled:hover {
    cursor: auto;
    background-color: #fff;
    color: rgb(207, 193, 193);
}

.usernames__matches {
    display: flex;
    flex-direction: column;
    width: 50%;
}
.section {
    display: flex;
    justify-content: center;
    min-height: 100vh;
    background-color: rgb(228, 228, 228);
}
.spinner {
  z-index: 800;
}
.lds-roller {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  .lds-roller div {
    animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    transform-origin: 40px 40px;
  }
  .lds-roller div:after {
    content: " ";
    display: block;
    position: absolute;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background: rgb(0, 0, 0);
    margin: -4px 0 0 -4px;
  }
  .lds-roller div:nth-child(1) {
    animation-delay: -0.036s;
  }
  .lds-roller div:nth-child(1):after {
    top: 63px;
    left: 63px;
  }
  .lds-roller div:nth-child(2) {
    animation-delay: -0.072s;
  }
  .lds-roller div:nth-child(2):after {
    top: 68px;
    left: 56px;
  }
  .lds-roller div:nth-child(3) {
    animation-delay: -0.108s;
  }
  .lds-roller div:nth-child(3):after {
    top: 71px;
    left: 48px;
  }
  .lds-roller div:nth-child(4) {
    animation-delay: -0.144s;
  }
  .lds-roller div:nth-child(4):after {
    top: 72px;
    left: 40px;
  }
  .lds-roller div:nth-child(5) {
    animation-delay: -0.18s;
  }
  .lds-roller div:nth-child(5):after {
    top: 71px;
    left: 32px;
  }
  .lds-roller div:nth-child(6) {
    animation-delay: -0.216s;
  }
  .lds-roller div:nth-child(6):after {
    top: 68px;
    left: 24px;
  }
  .lds-roller div:nth-child(7) {
    animation-delay: -0.252s;
  }
  .lds-roller div:nth-child(7):after {
    top: 63px;
    left: 17px;
  }
  .lds-roller div:nth-child(8) {
    animation-delay: -0.288s;
  }
  .lds-roller div:nth-child(8):after {
    top: 56px;
    left: 12px;
  }
  @keyframes lds-roller {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
.username--loading {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.8);
    z-index: 600;
  }
:root {
    --main-fonts:'Poppins', sans-serif;
}

body {
    margin: 0;
}

*, *::after, *::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: var(--main-fonts);
}
